import React, { Component } from 'react';

import AnimalsData from '../Data/AnimalsData.js';

export default class AnswersList extends Component {
    render() {

        

        const AnswerListItems = AnimalsData[this.props.level].map(item => {
            return (
            <li onClick={this.props.onClick} data-click-index={item.animal_index} className="answers-list-item" key={item.animal_name}>{item.animal_name}</li>
            )
        });

        return (
            <div className="answers">
                <ul className="answers-list">
                    {AnswerListItems}
                </ul>
            </div>
        )
    }
}
