import React, { Component } from 'react';


import AnimalsData from '../Data/AnimalsData.js';


export default class Answer extends Component {
    render() {

        const isClicked = this.props.isClicked;

        return (
            <div className="answer">
                {isClicked ? (
                    <>
                        <div className="answer-bird">
                            <div className="answer-bird-img">
                                <img src={AnimalsData[this.props.level][this.props.bird_index].animal_image} alt="" />
                            </div>
                            <div className="answer-bird-content">
                                <h3 className="answer-bird-content-title">
                                    {AnimalsData[this.props.level][this.props.bird_index].animal_name}
                                </h3>
                                <h4 className="answer-bird-content-subtitle">
                                    {AnimalsData[this.props.level][this.props.bird_index].species}
                                </h4>
                                <audio src={AnimalsData[this.props.level][this.props.bird_index].animal_audio} controls></audio>
                            </div>
                        </div>
                        <p className="answer-bird-description">
                            {AnimalsData[this.props.level][this.props.bird_index].animal_description}
                        </p>
                    </>
                ) : (
                        <>
                            <p>Послушайте плеер</p>
                            <p>Выберите животное из списка </p>
                        </>
                    )}

            </div>
        )
    }
}
