import React, { Component } from 'react';
import './styles/App.scss';
import 'bootstrap/dist/css/bootstrap.min.css';

import { Container, Row, Col } from 'react-bootstrap';

import right_sound from './audio/sound-right.mp3';
import wrong_sound from './audio/sound-wrong.mp3';

import Header from './Components/_Header/Header.js';
import Question from './Components/QuestionBlock/BirdQuestion.js';
import AnswersList from './Components/AnswersListBlock/AnswersList.js';
import Answer from './Components/AnswerBlock/Answer.js';
import NextButton from './Components/NextButton/NextButton.js';
import GameOver from './Components/GameOver/GameOver.js';


export default class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      score: 0,
      virtual_score: 5,
      level: 0,
      randomizer: 0,
      bird_index: 0,
      answer_list_clicked: false,
      answer_checked: false,
      game_over: false
    };

    this.onAnswerClick = this.onAnswerClick.bind(this);
    this.onNextButtonClick = this.onNextButtonClick.bind(this);
    this.gameAgain = this.gameAgain.bind(this);
  }

  componentDidMount() {
    this.setState({
      randomizer: Math.floor(Math.random() * 6)
    });
  }

  onAnswerClick(e) {
    let index = Number(e.target.attributes['data-click-index'].value);


    if (this.state.answer_checked === false) {
      e.target.className = 'answers-list-item wrong';

      this.audio = new Audio(wrong_sound);
      this.audio.play();
    }

    this.setState({
      answer_list_clicked: true,
      bird_index: index,
      virtual_score: this.state.virtual_score - 1
    });

    if (index === this.state.randomizer) {
      let answer_score = this.state.virtual_score;
      this.setState({
        answer_checked: true,
        score: this.state.score + answer_score
      });
      e.target.className = 'answers-list-item right';

      if (this.audio) {
        this.audio.pause()
      }
      this.audio = new Audio(right_sound);
      this.audio.play();
    }
  }

  onNextButtonClick() {
    this.setState({
      level: this.state.level + 1,
      bird_index: 0,
      answer_list_clicked: false,
      answer_checked: false,
      virtual_score: 5,
      randomizer: Math.floor(Math.random() * 6)
    });

    if (this.state.level === 5 && this.state.answer_checked === true) {
      this.setState({
        level: 0,
        game_over: true
      });
    }
  }

  gameAgain() {
    this.setState({
      score: 0,
      virtual_score: 5,
      randomizer: 0,
      bird_index: 0,
      answer_list_clicked: false,
      answer_checked: false,
      game_over: false
    });
  }


  render() {
    if (this.state.game_over) {
      return (
        <div className="App">
          <Container>
            <Header
              active_level={this.state.level}
              score={this.state.score}
            />

            <GameOver
              onClick={this.gameAgain}
              final_score={this.state.score}
            />
          </Container>
        </div>
      )
    } else {
      return (
        <div className="App">
          <Container>
            <Header
              active_level={this.state.level}
              score={this.state.score}
            />
            <Row className="justify-content-center">
              <Col lg={12}>
                <Question
                  level={this.state.level}
                  isChecked={this.state.answer_checked}
                  randomizer={this.state.randomizer}
                  bird_question_autoplay={this.state.answer_checked ? false : false}
                />
              </Col>
              <Col lg={6}>
                <AnswersList
                  level={this.state.level}
                  onClick={this.onAnswerClick}
                />
              </Col>
              <Col lg={6}>
                <Answer
                  level={this.state.level}
                  bird_index={this.state.bird_index}
                  isClicked={this.state.answer_list_clicked}
                  answer_autoplay={this.state.answer_checked ? false : false}
                />
              </Col>
            </Row>

            <NextButton
              onClick={this.state.answer_checked ? this.onNextButtonClick : () => { }}
              className={this.state.answer_checked ? 'next-button active' : 'next-button'}
            />
          </Container>
        </div>
      )
    }
  }
}

