import React, { Component } from 'react';

import NoNameAnimal from '../../media/no-name-animals.png';


import AnimalsData from '../Data/AnimalsData.js';



export default class BirdQuestion extends Component {
    render() {
        return (
            <div className="bird-question">
                <div className="bird-question-image">
                    <img src={this.props.isChecked ? AnimalsData[this.props.level][this.props.randomizer].animal_image : NoNameAnimal} alt=""/>
                </div>
                <div className="bird-question-content">
                    <h2 className="bird-question-content-title">
                        {this.props.isChecked ? AnimalsData[this.props.level][this.props.randomizer].animal_name : '*****'}
                    </h2>
                    <audio src={AnimalsData[this.props.level][this.props.randomizer].animal_audio} play controls></audio>
                </div>
            </div>
        )
    }
}
